import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ContactDetailComponent } from './contact-detail/contact-detail.component';
import { CreateContactComponent } from './create-contact/create-contact.component';
import { ContactListComponent } from './contact-list/contact-list.component';

import { ImconfigDetailsComponent } from './imconfig-details/imconfig-details.component';
import { CpuLineChartComponent } from './cpu-line-chart/cpu-line-chart.component';
import { MemoryChartComponent } from './memory-chart/memory-chart.component';
import { StorageChartComponent } from './storage-chart/storage-chart.component';



const routes: Routes = [
  { path: '', redirectTo: 'imconfigs', pathMatch: 'full' },
  { path: 'contacts', component: ContactListComponent },
  { path: 'add', component: CreateContactComponent },
  { path: 'details/:id', component: ContactDetailComponent },

  { path: 'cpu', component: CpuLineChartComponent },
  { path: 'memory', component: MemoryChartComponent },
  { path: 'storage', component: StorageChartComponent },
  { path: 'imconfigs', component: ImconfigDetailsComponent }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
