import { Component, OnInit, ViewChild } from '@angular/core';
import { ChartDataSets, ChartOptions } from 'chart.js';
import { Color, Label } from 'ng2-charts';
import { ActivatedRoute, Router } from '@angular/router';
import { IMConfigService } from "../imconfig.service";
import { timer } from 'rxjs';

@Component({
  selector: 'app-cpu-line-chart',
  templateUrl: './cpu-line-chart.component.html',
  styleUrls: ['./cpu-line-chart.component.css']
})
export class CpuLineChartComponent implements OnInit {

public canvasWidth = 300
public needleValue = 65
public centralLabel = ''
public name = 'CPU Load'
public bottomLabel = '65'
public options = {
    hasNeedle: true,
    needleColor: 'gray',
    needleUpdateSpeed: 1000,
    arcColors: ['rgb(44, 151, 222)', 'lightgray'],
    arcDelimiters: [30],
    rangeLabel: ['0', '100'],
    needleStartValue: 10,
}

  title = 'CPU Report';  
  type = 'LineChart';  
  data = [  
     ["2014", 200],  
     ["2015", 560],  
     ["2016", 280],  
     ["2017", 300],  
     ["2018", 600]  
  ];  
  columnNames = ['Year', 'India'];   
  width = 600;  
  height = 400;

  constructor(private route: ActivatedRoute,private router: Router,
    private imconfigService: IMConfigService) { }

  ngOnInit() {
    timer(0, 5000).pipe().subscribe(data => {
      this.reloadData();
    });
   
  }

  reloadData() {
    this.imconfigService.getCPUValue().subscribe(data => {
      console.log(data)
      this.needleValue = data;
      this.bottomLabel=data;
      this.options.arcDelimiters=[data];
      this.options.needleStartValue=data;
    }, error => console.log(error));
  }
}
